.intro{
    background-color: white;
    display: flex;
    
   
    .left{
        flex: 0.5;
        padding-top: 45px;
        padding-left: 60px;

        .imgContainer{
            transition: 1s ease;
            width: 800px;
            height: 800px;
            background-color: royalblue;
            border-radius: 90%;
            display: flex;
            align-items: flex-start;
            overflow: hidden;
            &.active{
                transition: all 1s ease;
                background-color: red;
            }



            img{
                width: 100%;
                transform: scaleX(-1);
                position: relative;
                top: -70px;

            }

        }
    }

    .right{
        flex: 0.5;
        transition: all 1s ease;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 60px;
            display: flex;
            flex-direction: column;
            justify-content: center;



            h1{
                font-size: 80px;
                margin: 10px 0;
            }
            h2{
                font-size: 45px;
            }
            h3{
                
                font-size: 30px;
                span{
                    transition: 1s ease;
                    color: royalblue;
                    &.active{
                        transition: all 1s ease;
                        color: red;
                    }
                }
                
                

                .ityped-cursor{
                    animation: blink 1s infinite;
                }
                @keyframes blink{
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;

            img{
                width: 30px;
                animation: arrowBlink 3s infinite;
            }

            @keyframes arrowBlink {
                50%{
                    opacity: 0;
                }
               
                
            }
        }
    }
}
