@import "../../global.scss";

.topbar{
    width: 100%;
    height: 70px;
    background-color: white;
    color: royalblue;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

.wrapper{
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    
    .right{
        .menu{
            width: 32px;
            height: 25px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            cursor: pointer;

            span{
                width: 100%;
                height: 3px;
                background-color: royalblue;
                transform-origin: left;
                transition: all 1s ease;
            }
        }
    }
    
    
    
    .left{
        display: flex;
        align-items: center;

        .logo{
            font-size: 40px;
            font-weight: 600;
            text-decoration: none;
            color: inherit;
            margin-right: 10px;
            
        }
        .itemcontainter{
            display: flex;
            align-items: center;
            margin-left: 30px;
            


            .icon{
                font-size: 25px;
                margin-right: 0px;
            }
            .icon2{
                font-size: 25px;
                margin-left:20px;
            }
            span{
                font-size: 25px;
                font-weight: 500;
            }

        }
        
    }

}
&.active{
    background-color: black;
    color: red;

    .menu{
        span{
            &:first-child{
                background-color: red;
                transform: rotate(45deg);
            }
            &:nth-child(2){
                opacity: 0;
            }
            &:last-child{
                background-color: red;
                transform: rotate(-45deg);
            }
        }
    }
}
}
